'use client'

import { useState } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { 
  ArrowRight, 
  Check, 
  Terminal, 
  Zap, 
  CreditCard,
  Shield,
  AlertCircle,
  Eye,
  EyeOff,
  Copy,
  CheckCircle2
} from 'lucide-react'
import { NexFlowInfraStackLogo } from "@/components/nexflow-infra-stack-logo"

export default function StartPage() {
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [showPassword, setShowPassword] = useState(false)
  const [copied, setCopied] = useState(false)
  const [success, setSuccess] = useState<{
    accountId: string
    apiKey: { token: string }
    plan: { name: string; dailyLimit: number; monthlyLimit: number }
  } | null>(null)

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)

    try {
      const response = await fetch('/api/v1/auth/signup', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || data.details?.[0]?.message || 'Signup failed')
      }

      // Store the API key
      localStorage.setItem('api_key', data.apiKey.token)
      
      setSuccess({
        accountId: data.accountId,
        apiKey: data.apiKey,
        plan: data.plan,
      })
    } catch (err: any) {
      setError(err.message || 'Something went wrong')
    } finally {
      setLoading(false)
    }
  }

  if (success) {
    return (
      <div className="min-h-screen bg-gray-950 text-white">
        {/* Navigation */}
        <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
          <div className="max-w-6xl mx-auto px-4">
            <div className="flex items-center justify-between h-16">
              <Link href="/">
                <NexFlowInfraStackLogo />
              </Link>
            </div>
          </div>
        </nav>

        <main className="py-20">
          <div className="max-w-2xl mx-auto px-4">
            <div className="text-center mb-8">
              <div className="w-16 h-16 bg-lime-400/10 rounded-full flex items-center justify-center mx-auto mb-6">
                <Check className="w-8 h-8 text-lime-400" />
              </div>
              <h1 className="text-4xl font-bold mb-4">Welcome to NexFlow!</h1>
              <p className="text-xl text-gray-400">
                Your account has been created successfully.
              </p>
            </div>

            {/* API Key Display */}
            <div className="bg-gray-900 p-8 rounded-2xl border border-gray-800 mb-8">
              <div className="flex items-center gap-3 mb-4">
                <Zap className="w-5 h-5 text-lime-400" />
                <h2 className="text-xl font-semibold">Your API Key</h2>
              </div>
              <p className="text-gray-400 text-sm mb-4">
                Save this key now — it won't be shown again.
              </p>
              <div className="bg-gray-800 p-4 rounded-lg font-mono text-sm break-all text-lime-400 border border-gray-700">
                {success.apiKey.token}
              </div>
              <button
                onClick={async () => {
                  navigator.clipboard.writeText(success.apiKey.token)
                  setCopied(true)
                  // Mark onboarding step as complete
                  try {
                    await fetch('/api/v1/user/onboarding', {
                      method: 'PUT',
                      headers: {
                        'Content-Type': 'application/json',
                        'x-api-key': success.apiKey.token,
                      },
                      body: JSON.stringify({ stepId: 'api_key_copied' }),
                    })
                  } catch (err) {
                    // Silently ignore - not critical
                  }
                }}
                className={`mt-4 px-4 py-2 rounded-lg text-sm transition-colors flex items-center gap-2 ${
                  copied 
                    ? 'bg-lime-400/20 text-lime-400 border border-lime-400/30' 
                    : 'bg-gray-800 hover:bg-gray-700 text-white'
                }`}
              >
                {copied ? (
                  <>
                    <CheckCircle2 className="w-4 h-4" />
                    Copied!
                  </>
                ) : (
                  <>
                    <Copy className="w-4 h-4" />
                    Copy to Clipboard
                  </>
                )}
              </button>
              {copied && (
                <p className="mt-2 text-xs text-lime-400">
                  ✓ API key saved to clipboard. Store it somewhere secure!
                </p>
              )}
            </div>

            {/* Plan Info */}
            <div className="bg-lime-400/5 border border-lime-400/20 p-6 rounded-xl mb-8">
              <div className="flex items-center gap-3 mb-3">
                <Shield className="w-5 h-5 text-lime-400" />
                <h3 className="font-semibold text-lime-400">Free Tier Activated</h3>
              </div>
              <p className="text-gray-300">
                You're on the <span className="text-lime-400 font-medium">{success.plan.name}</span> plan with:
              </p>
              <ul className="mt-3 space-y-2 text-gray-400">
                <li className="flex items-center gap-2">
                  <Check className="w-4 h-4 text-lime-400" />
                  {success.plan.dailyLimit.toLocaleString()} API calls per day
                </li>
                <li className="flex items-center gap-2">
                  <Check className="w-4 h-4 text-lime-400" />
                  {success.plan.monthlyLimit.toLocaleString()} API calls per month
                </li>
                <li className="flex items-center gap-2">
                  <Check className="w-4 h-4 text-lime-400" />
                  No credit card required
                </li>
              </ul>
            </div>

            {/* Getting Started Banner */}
            <div className="bg-blue-900/20 border border-blue-800/50 p-6 rounded-xl mb-8">
              <h3 className="font-semibold text-blue-300 mb-2">Getting Started</h3>
              <p className="text-blue-400/80 text-sm mb-4">
                Head to the dashboard to create your first metered endpoint. See the{' '}
                <Link href="/docs?section=metered" className="underline hover:text-blue-300">
                  endpoint setup guide
                </Link>{' '}
                for step-by-step instructions.
              </p>
              <div className="flex flex-wrap gap-4 text-sm">
                <Link
                  href="/dashboard"
                  className="text-blue-400 hover:text-blue-300 font-medium"
                >
                  Create your first endpoint →
                </Link>
                <Link
                  href="/dashboard/usage"
                  className="text-blue-400 hover:text-blue-300 font-medium"
                >
                  View usage dashboard →
                </Link>
              </div>
            </div>

            <div className="flex gap-4">
              <Link
                href="/dashboard"
                className="flex-1 bg-lime-400 text-gray-900 py-3 rounded-lg font-semibold hover:bg-lime-300 flex items-center justify-center gap-2 transition-colors"
              >
                Go to Dashboard <ArrowRight className="w-4 h-4" />
              </Link>
              <Link
                href="/docs?section=metered"
                className="flex-1 bg-gray-800 text-white py-3 rounded-lg font-semibold hover:bg-gray-700 flex items-center justify-center gap-2 transition-colors"
              >
                Endpoint Setup Guide
              </Link>
            </div>
          </div>
        </main>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-950 text-white">
      {/* Navigation */}
      <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <Link href="/">
              <NexFlowInfraStackLogo />
            </Link>
            <Link href="/" className="text-gray-400 hover:text-white">
              ← Back to Home
            </Link>
          </div>
        </div>
      </nav>

      <main className="py-20">
        <div className="max-w-2xl mx-auto px-4">
          {/* Hero Section */}
          <div className="text-center mb-12">
            <h1 className="text-4xl font-bold mb-4">
              Start Building with NexFlow
            </h1>
            <p className="text-xl text-gray-400">
              Instant free tier. No credit card required.
            </p>
          </div>

          {/* Free Tier Info Box */}
          <div className="bg-lime-400/5 border border-lime-400/20 p-4 rounded-xl mb-8">
            <div className="flex items-start gap-3">
              <Zap className="w-5 h-5 text-lime-400 mt-0.5" />
              <div>
                <p className="text-lime-400 font-medium">Free Tier Includes</p>
                <p className="text-gray-400 text-sm mt-1">
                  1,000 API calls/day • 10,000 calls/month • Upgrade anytime when you need more
                </p>
              </div>
            </div>
          </div>

          {/* Signup Form */}
          <div className="bg-gray-900 p-8 rounded-2xl border border-gray-800 mb-8">
            <h2 className="text-xl font-semibold mb-6">Create Your Account</h2>

            {error && (
              <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
                <AlertCircle className="w-5 h-5 text-red-400" />
                <span className="text-red-400">{error}</span>
              </div>
            )}

            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-300 mb-1">
                  Name <span className="text-gray-500">(optional)</span>
                </label>
                <input 
                  type="text" 
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white"
                  placeholder="Your name or company"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-300 mb-1">
                  Email *
                </label>
                <input 
                  type="email" 
                  required
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white"
                  placeholder="you@company.com"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-300 mb-1">
                  Password *
                </label>
                <div className="relative">
                  <input 
                    type={showPassword ? 'text' : 'password'}
                    required
                    minLength={8}
                    value={formData.password}
                    onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                    className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white pr-12"
                    placeholder="Min 8 characters, 1 uppercase, 1 number"
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  Must include uppercase, lowercase, and a number
                </p>
              </div>

              <button 
                type="submit"
                disabled={loading}
                className="w-full bg-lime-400 text-gray-900 py-3 rounded-lg font-semibold hover:bg-lime-300 flex items-center justify-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {loading ? (
                  <>
                    <div className="w-4 h-4 border-2 border-gray-900/30 border-t-gray-900 rounded-full animate-spin" />
                    Creating Account...
                  </>
                ) : (
                  <>
                    Get Started Free <ArrowRight className="w-4 h-4" />
                  </>
                )}
              </button>
            </form>

            <div className="mt-6 pt-6 border-t border-gray-800">
              <p className="text-center text-sm text-gray-500">
                Already have an account?{' '}
                <Link href="/dashboard" className="text-lime-400 hover:text-lime-300">
                  Sign in
                </Link>
              </p>
            </div>
          </div>

          {/* Quick Start */}
          <div className="bg-gray-900 p-6 rounded-xl border border-gray-800 mb-8">
            <div className="flex items-center gap-2 mb-4">
              <Terminal className="w-5 h-5 text-blue-400" />
              <h3 className="font-semibold">Quick Start</h3>
            </div>
            <pre className="bg-gray-800 p-4 rounded-lg text-sm overflow-x-auto text-gray-300">
{`# Install the SDK
npm install @nexflow/sdk

# Create a metered endpoint
const endpoint = await nexflow.endpoints.create({
  name: 'My API',
  upstreamUrl: 'https://api.example.com',
  pricePerCall: '0.001' // USDC
})`}
            </pre>
          </div>

          {/* What You Get */}
          <div className="space-y-3">
            <div className="flex items-center gap-3 text-gray-300">
              <Check className="w-5 h-5 text-lime-400" />
              <span>Free tier: 1,000 API calls/day, 10,000/month</span>
            </div>
            <div className="flex items-center gap-3 text-gray-300">
              <Check className="w-5 h-5 text-lime-400" />
              <span>x402 payment verification built-in</span>
            </div>
            <div className="flex items-center gap-3 text-gray-300">
              <Check className="w-5 h-5 text-lime-400" />
              <span>Real-time usage monitoring dashboard</span>
            </div>
            <div className="flex items-center gap-3 text-gray-300">
              <Check className="w-5 h-5 text-lime-400" />
              <span>Upgrade when you hit limits — no lock-in</span>
            </div>
          </div>

          <p className="text-center text-sm text-gray-500 mt-8">
            By signing up, you agree to our{' '}
            <Link href="/terms" className="text-lime-400 hover:underline">Terms of Service</Link>
            {' '}and{' '}
            <Link href="/privacy" className="text-lime-400 hover:underline">Privacy Policy</Link>.
          </p>
        </div>
      </main>

      {/* Footer */}
      <footer className="border-t border-gray-800 py-8 mt-20">
        <div className="max-w-6xl mx-auto px-4 text-center">
          <p className="text-gray-600 text-sm">© 2025 NexFlow. Smart payment infrastructure.</p>
        </div>
      </footer>
    </div>
  )
}
